(function() {
	// Creo el arreglo de tags
	let tags = []
	// Selecciono el campo de tags
	const tagsInput = document.querySelector('#tags_input')
	const tagsDiv = document.querySelector('#tags')
	const tagsInputHidden = document.querySelector('[name="tags"]')
	// Si el campo esta presente en la pagina:
	if(tagsInput) {
		// Creo un evento tipo keypress y llamo la funcion
		tagsInput.addEventListener('keypress', guardarTag)
	}
	// Funcion para guardar los tags
	function guardarTag(e) {
		// Si presiono una coma (','):
		if(e.keyCode === 44) {
			// Si no hay nada escrito
			if(e.target.value.trim() === '' || e.target.value < 1) {
				// La coma no se escribe
				e.preventDefault()
				// Corto la ejecucion
				return
			}
			// La coma no se escribe en el campo
			e.preventDefault()
			// Agrego el tag actual a tags eliminando espacios en blanco
			tags = [...tags, e.target.value.trim()]
			// Limpio el campo de tagsInput
			tagsInput.value = ''
			// Llamo la funcion mostrarTags
			mostrarTags()
		}	
	}
	// Funcion para mostrar los tags en el DOM
	function mostrarTags() {
		tagsDiv.textContent = ''
		tags.forEach(tag => {
			const etiqueta = document.createElement('LI')
			const btnEliminar = document.createElement('I')
			etiqueta.classList.add('formulario__tag')
			etiqueta.textContent = tag
			btnEliminar.classList.add('fa-solid', 'fa-square-minus', 'tag__eliminar')
			btnEliminar.onclick = eliminarTag
			etiqueta.appendChild(btnEliminar)
			tagsDiv.appendChild(etiqueta)
		})
		actualizarInputHidden()
	}
	// Funcion para eliminar Tags
	function eliminarTag(e) {
		e.target.parentElement.remove()
		tags = tags.filter(tag => tag !== e.target.parentElement.textContent)
		actualizarInputHidden()
	}
	// Funcion para actualizar los tags
	function actualizarInputHidden() {
		tagsInputHidden.value = tags.toString()
		console.log(tagsInputHidden.value)
	}
})()